import type { z, ZodType } from "zod/v4";
import { isFunction } from "es-toolkit/compat";
import type {
  FetchConfig,
  HttpMethod,
  QueryKey,
  NextTypeFetch,
} from "../../types";

/**
 * Zod     z.infer<T> ,  Fallback
 */
type InferIfZodSchema<T, Fallback> = [T] extends [ZodType]
  ? z.infer<T>
  : Fallback;

/**
 *  Mutation  (url + method )
 */
interface BaseMutationConfig<
  TVariables = any,
  TData = any,
  TError = Error,
  TContext = unknown,
  RequestSchema extends ZodType = never,
  ResponseSchema extends ZodType = never
> {
  /**
   * Mutation   . ()
   * Devtools    .
   */
  cacheKey?: QueryKey;

  /**
   *      Zod . ()
   */
  requestSchema?: RequestSchema;

  /**
   *      Zod . ()
   *     TData  .
   */
  responseSchema?: ResponseSchema;

  /**
   * Mutation     . ()
   *   onSuccess, onError, onSettled   .
   */
  onMutate?: (
    variables: TVariables
  ) => Promise<TContext | void> | TContext | void;

  /**
   * Mutation    . ()
   */
  onSuccess?: (
    data: InferIfZodSchema<ResponseSchema, TData>,
    variables: TVariables,
    context: TContext | undefined
  ) => Promise<void> | void;

  /**
   * Mutation    . ()
   */
  onError?: (
    error: TError,
    variables: TVariables,
    context: TContext | undefined
  ) => Promise<void> | void;

  /**
   * Mutation        . ()
   */
  onSettled?: (
    data: InferIfZodSchema<ResponseSchema, TData> | undefined,
    error: TError | null,
    variables: TVariables,
    context: TContext | undefined
  ) => Promise<void> | void;

  /**
   * Mutation            . ()
   */
  invalidateQueries?:
    | QueryKey[]
    | ((
        data: InferIfZodSchema<ResponseSchema, TData>,
        variables: TVariables,
        context: TContext | undefined
      ) => QueryKey[]);

  /**
   *  Mutation   fetch  . ()
   * baseURL, timeout, headers    .
   */
  fetchConfig?: Omit<
    FetchConfig,
    "url" | "method" | "params" | "data" | "schema"
  >;
}

/**
 * URL + Method  Mutation 
 */
interface UrlBasedMutationConfig<
  TVariables = any,
  TData = any,
  TError = Error,
  TContext = unknown,
  RequestSchema extends ZodType = never,
  ResponseSchema extends ZodType = never
> extends BaseMutationConfig<
    TVariables,
    TData,
    TError,
    TContext,
    RequestSchema,
    ResponseSchema
  > {
  /**
   * API  URL    .
   * TVariables   URL  .
   */
  url: string | ((variables: TVariables) => string);

  /**
   * HTTP  . (: "POST", "PUT", "DELETE")
   */
  method: HttpMethod;

  /**
   * mutationFn  
   */
  mutationFn?: never;
}

/**
 * Custom Function  Mutation 
 */
interface FunctionBasedMutationConfig<
  TVariables = any,
  TData = any,
  TError = Error,
  TContext = unknown,
  RequestSchema extends ZodType = never,
  ResponseSchema extends ZodType = never
> extends BaseMutationConfig<
    TVariables,
    TData,
    TError,
    TContext,
    RequestSchema,
    ResponseSchema
  > {
  /**
   *   mutation .
   * variables fetcher       .
   */
  mutationFn: (
    variables: TVariables,
    fetcher: NextTypeFetch
  ) => Promise<InferIfZodSchema<ResponseSchema, TData>>;

  /**
   * url/method  
   */
  url?: never;
  method?: never;
}

/**
 * Mutation     .
 * URL + Method   Custom Function      .
 */
export type MutationConfig<
  TVariables = any,
  TData = any,
  TError = Error,
  TContext = unknown,
  RequestSchema extends ZodType = never,
  ResponseSchema extends ZodType = never
> =
  | UrlBasedMutationConfig<
      TVariables,
      TData,
      TError,
      TContext,
      RequestSchema,
      ResponseSchema
    >
  | FunctionBasedMutationConfig<
      TVariables,
      TData,
      TError,
      TContext,
      RequestSchema,
      ResponseSchema
    >;

/**
 * MutationFactory   .
 *    mutation ,   mutation MutationConfig.
 */
export type MutationFactoryInput = Record<
  string,
  MutationConfig<any, any, any, any, any, any>
>;

/**
 * MutationConfig TVariables  .
 */
export type ExtractMutationVariables<T> = T extends MutationConfig<
  infer V,
  any,
  any,
  any,
  any,
  any
>
  ? V
  : never;

/**
 * MutationConfig TData  .
 * responseSchema     ,  TData .
 */
export type ExtractMutationData<T> = T extends MutationConfig<
  any,
  infer D,
  any,
  any,
  any,
  infer RS
>
  ? [RS] extends [ZodType]
    ? z.infer<RS>
    : D
  : never;

/**
 * MutationConfig TError  .
 */
export type ExtractMutationError<T> = T extends MutationConfig<
  any,
  any,
  infer E,
  any,
  any,
  any
>
  ? E
  : Error;

/**
 *   
 */
const ERROR_MESSAGES = {
  BOTH_APPROACHES:
    "MutationConfig cannot have both 'mutationFn' and 'url'+'method' at the same time. " +
    "Choose either custom function approach (mutationFn) or URL-based approach (url + method).",
  MISSING_APPROACHES:
    "MutationConfig must have either 'mutationFn' or both 'url' and 'method'. " +
    "Provide either a custom function or URL-based configuration.",
} as const;

/**
 * Mutation   
 */
export function validateMutationConfig(
  config: MutationConfig<any, any, any, any, any, any>
): void {
  const hasMutationFn = isFunction(config.mutationFn);
  const hasUrlMethod = config.url && config.method;

  if (hasMutationFn && hasUrlMethod) {
    throw new Error(ERROR_MESSAGES.BOTH_APPROACHES);
  }

  if (!hasMutationFn && !hasUrlMethod) {
    throw new Error(ERROR_MESSAGES.MISSING_APPROACHES);
  }
}

/**
 * Mutation       .
 *   ,  mutation    .
 * @param defs Mutation  
 * @returns  Mutation  
 */
export function createMutationFactory<T extends MutationFactoryInput>(
  defs: T
): T {
  //  MutationConfig 
  Object.entries(defs).forEach(([key, config]) => {
    try {
      validateMutationConfig(config);
    } catch (error) {
      throw new Error(
        `Invalid MutationConfig for '${key}': ${(error as Error).message}`
      );
    }
  });

  return defs;
}
