import { isFunction } from "es-toolkit";
import { FetchError, type NextTypeResponse } from "../types";

/**
 *  FetchError .
 * @param error  
 * @returns FetchError 
 *
 * @example
 * try {
 *   const response = await api.get('/api/users');
 *   //  
 * } catch (error) {
 *   if (isFetchError(error)) {
 *     console.error('API :', error.message, error.code);
 *   }
 * }
 */
export function isFetchError(error: unknown): error is FetchError {
  return error instanceof FetchError;
}

/**
 *       .
 * @param error  
 * @param code   
 * @returns  
 *
 * @example
 * if (hasErrorCode(error, 'ERR_CANCELED')) {
 *   console.log(' .');
 * }
 */
export function hasErrorCode(error: unknown, code: string): boolean {
  return isFetchError(error) && error.code === code;
}

/**
 * FetchError   
 */
export const ErrorCode = {
  /**   */
  NETWORK: "ERR_NETWORK",
  /**   */
  CANCELED: "ERR_CANCELED",
  /**   */
  TIMEOUT: "ERR_TIMEOUT",
  /**    (4xx, 5xx) */
  BAD_RESPONSE: "ERR_BAD_RESPONSE",
  /**    */
  VALIDATION: "ERR_VALIDATION",
  /**      */
  VALIDATION_UNKNOWN: "ERR_VALIDATION_UNKNOWN",
  /**     */
  UNKNOWN: "ERR_UNKNOWN",
} as const;

/**
 * FetchError   
 */
export type ErrorCodeType = (typeof ErrorCode)[keyof typeof ErrorCode];

/**
 *    
 * @param error  
 * @param handlers   
 * @returns   (  )
 *
 * @example
 * try {
 *   const response = await api.get('/api/users');
 *   return response.data;
 * } catch (error) {
 *   return handleFetchError(error, {
 *     [ErrorCode.NETWORK]: () => '  .',
 *     [ErrorCode.TIMEOUT]: () => '  .',
 *     [ErrorCode.CANCELED]: () => ' .',
 *     default: (err) => ` : ${err.message}`,
 *   });
 * }
 */
export function handleFetchError<T>(
  error: unknown,
  handlers: {
    [code in ErrorCodeType]?: (error: FetchError) => T;
  } & {
    default?: (error: unknown) => T;
  }
): T {
  if (isFetchError(error) && error.code) {
    const errorCode = error.code as ErrorCodeType;
    const handler = handlers[errorCode];
    if (handler) {
      return handler(error);
    }
  }

  if (handlers.default) {
    return handlers.default(error);
  }

  //     throw
  throw error;
}

/**
 * HTTP     .
 * @param error  
 * @param handlers HTTP   
 * @returns   (  )
 *
 * @example
 * try {
 *   const response = await api.get('/api/users');
 *   return response.data;
 * } catch (error) {
 *   return handleHttpError(error, {
 *     400: () => ' .',
 *     401: () => ' .',
 *     404: () => '   .',
 *     default: () => '  .',
 *   });
 * }
 */
export function handleHttpError<T>(
  error: unknown,
  handlers: {
    [status: number]: (error: FetchError) => T;
    default?: (error: unknown) => T;
  }
): T {
  if (
    isFetchError(error) &&
    error.response &&
    isFunction(handlers[error.response.status])
  ) {
    return handlers[error.response.status](error);
  }

  if (handlers.default) {
    return handlers.default(error);
  }

  //     throw
  throw error;
}

/**
 *  NextTypeResponse  .
 *         .
 * @param error  
 * @param data  
 * @returns NextTypeResponse 
 *
 * @example
 * //   
 * api.interceptors.error.use((error) => {
 *   if (hasErrorCode(error, ErrorCode.VALIDATION)) {
 *     return errorToResponse(error, {
 *       validationError: true,
 *       fields: error.response?.data
 *     });
 *   }
 *   throw error;
 * });
 */
export function errorToResponse<T>(
  error: FetchError,
  data: T
): NextTypeResponse<T> {
  return {
    data,
    status: error.response?.status || 500,
    statusText: error.response?.statusText || error.message,
    headers: error.response?.headers || new Headers(),
    config: error.config,
    request: error.request,
  };
}
