import type { z } from "zod/v4";

/**
 *   
 */
export enum ContentType {
  /**
   * JSON  (application/json)
   */
  JSON = "application/json",

  /**
   * URL    (application/x-www-form-urlencoded)
   */
  FORM = "application/x-www-form-urlencoded",

  /**
   *   (text/plain)
   */
  TEXT = "text/plain",

  /**
   *   (application/octet-stream)
   */
  BLOB = "application/octet-stream",

  /**
   *    (multipart/form-data)
   */
  MULTIPART = "multipart/form-data",

  /**
   * XML  (application/xml)
   */
  XML = "application/xml",

  /**
   * HTML  (text/html)
   */
  HTML = "text/html",
}

/**
 *   
 */
export enum ResponseType {
  /**
   * JSON  ( )
   */
  JSON = "json",

  /**
   *   (text/plain, HTML, XML )
   */
  TEXT = "text",

  /**
   * Blob  (,    )
   */
  BLOB = "blob",

  /**
   * ArrayBuffer  ( )
   */
  ARRAY_BUFFER = "arraybuffer",

  /**
   *   (Response   )
   */
  RAW = "raw",
}

/**
 * HTTP  
 */
export type HttpMethod =
  | "GET"
  | "POST"
  | "PUT"
  | "DELETE"
  | "PATCH"
  | "HEAD"
  | "OPTIONS";

/**
 * HTTP  
 */
export class FetchError extends Error {
  /**
   *  
   */
  name = "FetchError";

  /**
   *  
   */
  code?: string;

  /**
   *   (HTTP  )
   */
  response?: {
    data: unknown;
    status: number;
    statusText: string;
    headers: Headers;
  };

  /**
   *  
   */
  request?: Request;

  /**
   *  
   */
  config: RequestConfig;

  /**
   * FetchError 
   * @param message  
   * @param config  
   * @param code  
   * @param request  
   * @param response  
   * @param responseData  
   */
  constructor(
    message: string,
    config: RequestConfig,
    code?: string,
    request?: Request,
    response?: Response,
    responseData?: unknown
  ) {
    super(message);
    this.config = config;
    this.code = code;
    this.request = request;

    if (response) {
      this.response = {
        data: responseData,
        status: response.status,
        statusText: response.statusText,
        headers: response.headers,
      };
    }
  }
}

/**
 * 401     
 */
export interface AuthRetryOption {
  /**
   *    (: 1)
   */
  limit?: number;
  /**
   *  HTTP    (: [401])
   *   (: 401004 )  
   */
  statusCodes?: number[];
  /**
   *     (true   )
   */
  handler: (error: FetchError, config: RequestConfig) => Promise<boolean>;
  /**
   *     (true   handler )
   *       
   */
  shouldRetry?: (error: FetchError, config: RequestConfig) => boolean;
}

/**
 *    
 */
export interface FetchConfig
  extends Omit<RequestInit, "signal" | "headers" | "body" | "method"> {
  /**
   *  URL
   */
  baseURL?: string;

  /**
   *   (ms)
   */
  timeout?: number;

  /**
   *  
   */
  headers?: Record<string, string>;

  /**
   *   
   */
  params?: Record<string, string | number | boolean | undefined | null>;

  /**
   *   
   */
  retry?:
    | number
    | {
        limit: number; //   
        statusCodes?: number[]; //   
        backoff?: "linear" | "exponential" | ((retryCount: number) => number);
      };

  /**
   *  JSON  
   * @deprecated responseType 
   */
  parseJSON?: boolean;

  /**
   *     Zod 
   */
  schema?: z.ZodType;

  /**
   * Next.js fetch 
   */
  next?: {
    /**
     *  ()
     */
    revalidate?: number | false;
    /**
     *      
     */
    tags?: string[];
  };

  /**
   *    AbortSignal
   *  AbortController     .
   */
  signal?: AbortSignal;

  /**
   *   
   *   Content-Type .
   */
  contentType?: ContentType | string;

  /**
   *   
   *     .
   */
  responseType?: ResponseType;

  /**
   * 401     
   */
  authRetry?: AuthRetryOption;
}

/**
 *     
 */
export interface RequestConfig extends FetchConfig {
  /**
   *  URL
   */
  url?: string;

  /**
   * HTTP 
   */
  method?: HttpMethod;

  /**
   *  
   */
  data?: unknown;

  /**
   * : 401  
   */
  _authRetryCount?: number;
}

/**
 *   
 */
export interface NextTypeResponse<T = unknown> {
  /**
   *   
   */
  data: T;

  /**
   * HTTP  
   */
  status: number;

  /**
   * HTTP  
   */
  statusText: string;

  /**
   *  
   */
  headers: Headers;

  /**
   *  
   */
  config: RequestConfig;

  /**
   *  
   */
  request?: Request;
}

/**
 *   
 */
export type RequestInterceptor = (
  config: RequestConfig
) => Promise<RequestConfig> | RequestConfig;

export type ResponseInterceptor = (
  response: NextTypeResponse
) => Promise<NextTypeResponse> | NextTypeResponse;

export type ErrorInterceptor = (
  error: FetchError
) => Promise<NextTypeResponse | FetchError> | NextTypeResponse | FetchError;

/**
 *   
 *    //   .
 */
export type QueryKey = string | readonly unknown[];

/**
 *       
 */
export interface InterceptorHandle {
  /**
   *   
   */
  remove: () => void;
}

/**
 *  
 */
export interface Interceptors {
  request: {
    use: (interceptor: RequestInterceptor) => InterceptorHandle;
    eject: (id: number) => void;
    clearByType: (type: symbol) => void;
    clear: () => void;
  };
  response: {
    use: (onFulfilled: ResponseInterceptor) => InterceptorHandle;
    eject: (id: number) => void;
    clearByType: (type: symbol) => void;
    clear: () => void;
  };
  error: {
    use: (onRejected: ErrorInterceptor) => InterceptorHandle;
    eject: (id: number) => void;
    clearByType: (type: symbol) => void;
    clear: () => void;
  };
}

/**
 *    
 */
export interface CancelablePromise<T> extends Promise<T> {
  /**
   *   
   */
  cancel: () => void;

  /**
   *    
   */
  isCanceled: () => boolean;
}

/**
 * Next Type Fetch  
 */
export interface NextTypeFetch {
  /**
   *  
   */
  defaults: FetchConfig;

  /**
   * 
   */
  interceptors: Interceptors;

  /**
   * GET 
   */
  get: <T = unknown>(
    url: string,
    config?: FetchConfig
  ) => CancelablePromise<NextTypeResponse<T>>;

  /**
   * POST 
   */
  post: <T = unknown>(
    url: string,
    data?: unknown,
    config?: FetchConfig
  ) => CancelablePromise<NextTypeResponse<T>>;

  /**
   * PUT 
   */
  put: <T = unknown>(
    url: string,
    data?: unknown,
    config?: FetchConfig
  ) => CancelablePromise<NextTypeResponse<T>>;

  /**
   * DELETE 
   */
  delete: <T = unknown>(
    url: string,
    config?: FetchConfig
  ) => CancelablePromise<NextTypeResponse<T>>;

  /**
   * PATCH 
   */
  patch: <T = unknown>(
    url: string,
    data?: unknown,
    config?: FetchConfig
  ) => CancelablePromise<NextTypeResponse<T>>;

  /**
   * HEAD 
   */
  head: <T = unknown>(
    url: string,
    config?: FetchConfig
  ) => CancelablePromise<NextTypeResponse<T>>;

  /**
   * OPTIONS 
   */
  options: <T = unknown>(
    url: string,
    config?: FetchConfig
  ) => CancelablePromise<NextTypeResponse<T>>;

  /**
   *   
   */
  request: <T = unknown>(
    config: RequestConfig
  ) => CancelablePromise<NextTypeResponse<T>>;
}
